import React from 'react';
import { Link } from "react-router-dom";
import { AllUser, AllGeneralUser, UpdateAllUserAuth, UpdateAllGeneralUserAuth } from '../../services/user-service';
import { NotificationManager } from 'react-notifications';
import { Loading } from '../../components/loading';
import { Button } from 'react-bootstrap';


class Manage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            users: null,
            isAdmin: localStorage.getItem("isAdmin") === 'true',
            isSuperAdmin: localStorage.getItem("isSuperAdmin") === 'true',
        }
    }

    componentDidMount() {
        if (this.state.isSuperAdmin) {
            AllUser((result) => {
                this.setState({ users: result })
            })
        } else if (this.state.isAdmin) {
            AllGeneralUser((result) => {
                this.setState({ users: result })
            })
        }

    }

    UpdateAuth = () => {
        if (this.state.isSuperAdmin) {
            UpdateAllUserAuth(
                this.state.users,
                () => {
                    NotificationManager.success('成功', '更新權限', 3000);
                },
                () => {
                    NotificationManager.error('錯誤', '更新權限', 3000);
                }
            )
        } else if (this.state.isAdmin) {
            UpdateAllGeneralUserAuth(
                this.state.users,
                () => {
                    NotificationManager.success('成功', '更新權限', 3000);
                },
                () => {
                    NotificationManager.error('錯誤', '更新權限', 3000);
                }
            )
        }
    }

    CheckBoxValueChange(prop, value) {
        let index = value.target.getAttribute('data-key');
        let copydata = this.state.users;
        copydata[index][prop] = !copydata[index][prop];
        copydata[index].isChange = true;

        this.setState({ users: copydata })
    }

    render() {
        if (!this.state.users) return (<Loading />);

        let header = [];
        let body = [];

        if (this.state.isSuperAdmin) {
            header = [
                <div key={"header"} className="row mt-3 mb-3">
                    <div className="col-8 text-nowrap overflow-hidden text-truncate" key={1}>Email</div>
                    <div className="col-2 text-nowrap overflow-hidden text-truncate text-center" key={2}>啟動帳號</div>
                    <div className="col-2 text-nowrap overflow-hidden text-truncate text-center" key={3}>授權管理者</div>
                </div>,
                <hr key={"hr"} />,
            ];

            body = this.state.users.map((e, i) => (
                <div key={e.Id} className="row mt-3 mb-3">
                    <div className="col-8 text-nowrap overflow-hidden text-truncate">{e.email}</div>
                    <div className="col-2 text-nowrap overflow-hidden text-truncate text-center"><input type="checkbox" checked={e.isActive} data-key={i} onChange={(isActive) => this.CheckBoxValueChange('isActive', isActive)} /></div>
                    <div className="col-2 text-nowrap overflow-hidden text-truncate text-center"><input type="checkbox" checked={e.isAdmin} data-key={i} onChange={(isAdmin) => this.CheckBoxValueChange('isAdmin', isAdmin)} /></div>
                </div>
            ));
        } else if (this.state.isAdmin) {
            header = [
                <div key={"header"} className="row mt-3 mb-3">
                    <div className="col-9 text-nowrap overflow-hidden text-truncate" key={1}>Email</div>
                    <div className="col-3 text-nowrap overflow-hidden text-truncate text-center" key={2}>啟動帳號</div>
                </div>,
                <hr key={"hr"} />,
            ];
            body = this.state.users.map((e, i) => (
                <div key={e.Id} className="row mt-3 mb-3">
                    <div className="col-9 text-nowrap overflow-hidden text-truncate">{e.email}</div>
                    <div className="col-3 text-nowrap overflow-hidden text-truncate text-center"><input type="checkbox" checked={e.isActive} data-key={i} onChange={(isActive) => this.CheckBoxValueChange('isActive', isActive)} /></div>
                </div>
            ));
        }

        return (
            <div className="container">
                <div className="mx-auto shadow rounded mt-5 mb-5" style={{ maxWidth: '600px', width: '100%', padding: '40px 30px' }}>
                    <p className="fs-2 text-center">權限管理</p>
                    <div className="row">
                        {header}
                        {body.length === 0 ? "沒有資料" : body}
                        <div key={"footer"} className="col-12 p-3 text-center position-sticky bottom-0" style={{ backgroundColor: "white" }}>
                            <Button disabled={body.length === 0} className="me-3" onClick={this.UpdateAuth}>更新</Button>
                            <Link className="btn btn-danger" to={"../join"}>取消</Link>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Manage;